.tabs {
    margin-top: 2rem;
}
.tabs__buttons {
    display: flex;
    gap: 1rem;
    margin-bottom: 2rem;
}
.tabs__btn {
    padding: 1rem 2rem;
    background: var(--white);
    border-radius: 6rem;
    font-size: 1.6rem;
    cursor: pointer;
    transition: all 0.2s linear;
    font-family: 'FuturaPT';
    font-weight: 500;
    color: var(--default);
}
.tabs__btn.active {
    background: var(--blue);
    color: var(--white);
}
.tabs__tab {
    display: none;
}
.tabs__tab.active {
    display: block;
}

@media (hover:hover) {
    .tabs__btn:hover {
        background: var(--blue);
        color: var(--white);
    }
}
@media (hover:none) {
    .tabs__btn:active {
        background: var(--blue);
        color: var(--white);
    }
}
